import React, { useState } from "react";
import './AuthForm.css';
/* import {useLocation} from "react-router-dom";
 */
import PopupChoose from "../PopupChoose/PopupChoose";
import { IconChevronDown, IconChevronUp } from '@tabler/icons';
import clsx from 'clsx';


function AuthForm() {
  const [proj_type, setproj_type] = useState("miller");
  const [sw_liter, setsw_liter] = useState("A");

  const [from_date, setfrom_date] = useState("2017-09-07");
  const [from_time, setfrom_time] = useState("00:00");
  const [to_date, setto_date] = useState("2017-09-09");
  const [to_time, setto_time] = useState("00:00");
  const [ionoDate, setIonoDate] = useState();
  const [ionoTime, setIonoTime] = useState();
  const [auroral_date, setauroral_date] = useState();
  const [auroral_time, setauroral_time] = useState();
  const [sw_delta, setsw_delta] = useState(300);
  const [dataType, setDataType] = useState('FAC');
  const [hemisphere, setHemisphere] = useState('Northern')
  const [iono_bz, setiono_bz] = useState();
  const [iono_by, setiono_by] = useState();
  const [iono_kp, setiono_kp] = useState();
  const [iono_f107, setiono_f107] = useState();
  const [iono_doy, setiono_doy] = useState();
  const [iono_ut, setiono_ut] = useState();

  const [sw_channel, setsw_channel] = useState("n");
  const [obs_code, setobs_code] = useState();
  const [deg_radius, setdeg_radius] = useState(5);

  const [isProjOpen, setIsProjOpen] = useState(false);
  const [isSw_Litter, setIsSw_Litter] = useState(false);
  const [issw_channel, setIssw_channel] = useState(false);
  const [isObs_code, setIsObs_code] = useState(false);
  const [supermag_obs, setsupermag_obs] = useState(false);
  const [isDataType, setIsDataType] = useState(false);
  const [isHemisphere, setIsHemisphere] = useState(false);

  const [literA, setLitterA] = useState(false);
  const [literB, setLitterB] = useState(false);
  const [literC, setLitterC] = useState(false);
  const [literAC, setLitterAC] = useState(false);


  const [projCorMinD, setprojCorMinD] = useState();
  const [projCorMinS, setprojCorMinS] = useState();
  const [projCorMaxD, setprojCorMaxD] = useState();
  const [projCorMaxS, setprojCorMaxS] = useState();


  const [swCorMinD, setswCorMinD] = useState();
  const [swCorMinS, setswCorMinS] = useState();
  const [swCorMaxD, setswCorMaxD] = useState();
  const [swCorMaxS, setswCorMaxS] = useState();


  const [annotate_sw_value, setannotate_sw_value] = useState(false);
  const [ionomodel_n, setionomodel_n] = useState(false);
  const [ionomodel_s, setionomodel_s] = useState(false);
  const [auroral_n, setauroral_n] = useState(false);
  const [auroral_s, setauroral_s] = useState(false);
  const [cut_obs_swarm_value, setcut_obs_swarm_value] = useState(false);
  const [igrf_vector_diff, setigrf_vector_diff] = useState(false);
  const [measure_mu, setmeasure_mu] = useState(false);
  const [near_auroral_points, setnear_auroral_points] = useState(false);
  const [mag_grid, setmag_grid] = useState(false)

  const proj_typeArr = ["miller", "ortho_n", "ortho_s", "plot"]
  const sw_literArr = ["A", "B", "C", "AC"]
  const swChannelArr = ["n", "e", "c", "fac2"]
  const obs = ['AAA', 'ABG', 'ABK', 'AIA', 'API', 'ARS', 'ASC', 'ASP', 'BDV', 'BEL', 'BFO', 'BLC', 'BMT', 'BOU', 'BOX', 'BRD', 'BRW', 'BSL', 'CBB', 'CKI', 'CLF', 'CMO', 'CNB', 'CNH', 'CPL', 'CSY', 'CTA', 'CYG', 'CZT', 'DED', 'DLT', 'DMC', 'DOU', 'DRV', 'DUR', 'EBR', 'ESK', 'EYR', 'FCC', 'FRD', 'FRN', 'FUR', 'GAN', 'GCK', 'GDH', 'GNG', 'GUA', 'GUI', 'GZH', 'HAD', 'HBK', 'HER', 'HLP', 'HON', 'HRB', 'HRN', 'HUA', 'HYB', 'IQA', 'IRT', 'IZN', 'JAI', 'JCO', 'KAK', 'KDU', 'KEP', 'KHB', 'KIV', 'KMH', 'KNY', 'KOU', 'LER', 'LON', 'LRM', 'LVV', 'LYC', 'LZH', 'MAB', 'MAW', 'MBO', 'MCQ', 'MEA', 'MGD', 'MMB', 'NAQ', 'NCK', 'NEW', 'NGK', 'NUR', 'NVS', 'ORC', 'OTT', 'PAF', 'PAG', 'PEG', 'PET', 'PHU', 'PIL', 'PPT', 'PST', 'RES', 'SBA', 'SBL', 'SFS', 'SHE', 'SHU', 'SIT', 'SJG', 'SOD', 'SON', 'SPG', 'SPT', 'STJ', 'SUA', 'TAM', 'TDC', 'THL', 'THY', 'TRW', 'TSU', 'TTB', 'TUC', 'UPS', 'VAL', 'VIC', 'VNA', 'VOS', 'VSS', 'WIC', 'WMQ', 'WNG', 'YAK', 'YKC'];

  function onSubmit(e, text) {
    e.preventDefault();
     
    document.location.href = `http://aleph.gcras.ru/request/?proj_type=${proj_type}&plot_liter=${literA}:${literB}:${literC}:${literAC}&sw_liter=${sw_liter}&from_date=${from_date}T${from_time}:00&to_date=${to_date}T${to_time}:00&sw_channel=${sw_channel}&annotate_sw_value=${annotate_sw_value}&ionomodel_date=${ionoDate}T${ionoTime}:00&ionomodel_n=${ionomodel_n}&ionomodel_s=${ionomodel_s}&auroral_date=${auroral_date}T${auroral_time}:00&auroral_n=${auroral_n}&auroral_s=${auroral_s}&proj_extend_loc=${projCorMinD}:${projCorMaxD}:${projCorMinS}:${projCorMaxS}&swarm_poly_loc=${swCorMinD}:${swCorMaxD}:${swCorMinS}:${swCorMaxS}&cut_obs_swarm_value=${cut_obs_swarm_value}&obs_code=${obs_code}&deg_radius=${deg_radius}&igrf_vector_diff=${igrf_vector_diff}&measure_mu=${measure_mu}&near_auroral_points=${near_auroral_points}&sw_delta=${sw_delta}&mag_grid=${mag_grid}&supermag_obs=${supermag_obs}&dataType=${dataType}&hemisphere=${hemisphere}&iono_bz=${iono_bz}&iono_by=${iono_by}&iono_kp=${iono_kp}&iono_f107=${iono_f107}&iono_doy=${iono_doy}&iono_ut=${iono_ut}&txt_out=${text}`
  }


  function onProjTypeClick(i) {
    setproj_type(i);
  }

  function onSwLitter(i) {
    setsw_liter(i)
  }

  function onSwChannel(i) {
    setsw_channel(i);
  }

  function onObs(i) {
    setobs_code(i)
  }


  function openProjType() {
    setIsProjOpen(!isProjOpen)
  }

  function openSW_Litter() {
    setIsSw_Litter(!isSw_Litter)
  }

  function openSw_Channel() {
    setIssw_channel(!issw_channel)
  }

  function openObs() {
    setIsObs_code(!isObs_code)
  }


  function isPlot() {
    return proj_type === "plot"
  }

  function handleDateFrom(e) {
    setfrom_date(e.target.value)
  }

  function handleTimeFrom(e) {
    setfrom_time(e.target.value)
  }

  function handleDateTo(e) {
    setto_date(e.target.value)
  }

  function handleTimeTo(e) {
    setto_time(e.target.value)
  }

  function handleIonoDate(e) {
    setIonoDate(e.target.value)
  }

  function handleIonoTime(e) {
    setIonoTime(e.target.value)
  }

  function handleAurorDate(e) {
    setauroral_date(e.target.value)
  }

  function handleAurorTime(e) {
    setauroral_time(e.target.value)
  }

  function isDisabled() {
    if (auroral_n || auroral_s) {
      if ((!auroral_date || !auroral_time)) {
        return true
      } else {
        return false
      }
    } else if (proj_type === "plot") {
      if (!literA && !literB && !literC && !literAC) {
        return true
      } else {
        return false
      }
    } else {
      return false
    }
  }


  return (
    <main className="auth">
      <form onSubmit={onSubmit} className="auth__form">
        <h2 className="form-title form-title_type_auth">{true ? "Онлайн система для визуализации и анализа данных измерений продольных токов на спутниках SWARM" : "Рады видеть!"}</h2>
        <div style={{ display: "flex" }}>
          <div className="left">
            <div style={{ width: "60%" }} onClick={openProjType}>
              <label className={true ? "form-lable form-lable_type_auth" : "form-lable form-lable_visibility_hidden form-lable_type_auth"}>Выберите тип проекции (Miller, Orthographic north, Orthographic south) или отображене графика (plot)<div className="input auth__form__input">{proj_type ? proj_type : "выберите тип"}{isProjOpen ? <IconChevronDown /> : <IconChevronUp />}</div></label>
              <PopupChoose isOpen={isProjOpen} click={onProjTypeClick} arr={proj_typeArr} />
            </div>
            <div className={clsx({ "littersWrap_Disabled": true, "littersWrap": isPlot() })}>
              <label className="form-lable form-lable_type_auth">
                Выберите литер(ы) спутников SWARM-
                <div className="getChekbox">
                  <input onChange={() => setLitterA(!literA)} type="checkbox" id="how-get" />
                  <label className="check">A</label>
                </div><div className="getChekbox">
                  <input onChange={() => setLitterB(!literB)} type="checkbox" id="how-get" />
                  <label className="check">B</label>
                </div><div className="getChekbox">
                  <input onChange={() => setLitterC(!literC)} type="checkbox" id="how-get" />
                  <label className="check">C</label>
                </div><div className="getChekbox">
                  <input onChange={() => setLitterAC(!literAC)} type="checkbox" id="how-get" />
                  <label className="check">|AC|</label>
                </div>
              </label>
              {(!literA && !literB && !literC && !literAC) && <div style={{ color: 'red' }}>Выберите одно из значений</div>}
            </div>
            <div className={clsx({ "littersWrap_Disabled": true, "littersWrap": !isPlot() })} style={{ width: "60%" }} onClick={openSW_Litter}>
              <label className="form-lable form-lable_type_auth">Выберите интересующий спутник (SWARM-liter) или величину вектора разности Swarm A и Swarm C (|AC|)<div className="input auth__form__input">{sw_liter ? sw_liter : "выберите тип"}{isSw_Litter ? <IconChevronDown /> : <IconChevronUp />}</div></label>
              <PopupChoose isOpen={isSw_Litter} click={onSwLitter} arr={sw_literArr} />
            </div>
            <div className="date">
              <h3 className="dateTitle">с даты</h3>
              <input onChange={handleDateFrom} type="date" defaultValue="2017-09-07" className="date_input" required />
              <input onChange={handleTimeFrom} type="time" defaultValue="00:00:00" className="date_input" required></input>
              <p>UTC</p>
            </div>
            {/* <label className="form-lable form-lable_type_auth">from_date<input autoComplete="off" type="text" name="from_date" className="auth__form__input" required={true ? true : ''} /></label> */}
            <div className="date">
              <h3 className="dateTitle">по дату</h3>
              <input onChange={handleDateTo} type="date" defaultValue="2017-09-09" className="date_input" required></input>
              <input onChange={handleTimeTo} type="time" defaultValue="00:00:00" className="date_input" required></input>
              <p>UTC</p>
            </div>
            <div className="littersWrap" style={{ width: "30%" }} onClick={openSw_Channel}>
              <label className="form-lable form-lable_type_auth">компонента вектора (N, E, C, FAC2)<div className="input auth__form__input">{sw_channel ? sw_channel : "выберите тип"}{issw_channel ? <IconChevronDown /> : <IconChevronUp />}</div></label>
              <PopupChoose isOpen={issw_channel} click={onSwChannel} arr={swChannelArr} />
            </div>
            <lable style={{ color: "white" }}>средний интервал, в секундах<input onChange={(e) => setsw_delta(e.target.value)} type="number" className="cor_input" style={{ margin: "0 0 0 10px" }} defaultValue={300}></input></lable>
            {!isPlot() && <div style={{ margin: "15px 0 19px 0" }} className="getChekbox">
              <input onChange={() => setannotate_sw_value(!annotate_sw_value)} type="checkbox" id="how-get" />
              <label className="check">Отображать числовые значения пролетов спутника SWARM</label>
            </div>}
            {isPlot() && <div style={{ margin: "15px 0 19px 0" }} className="getChekbox">
              <input onChange={() => setsupermag_obs(!supermag_obs)} type="checkbox" id="how-get" />
              <label className="check">Отображение значений наземных станций superMAG</label>
            </div>}
          </div>
          <div className="right">
            <div className="date" style={{ display: "flex", flexDirection: "column" }}>
              <h3 className="dateTitle">Если требуется отображение модели электромагнитных параметров приполярной ионосферы (http://geomag.gcras.ru/dataserv-ionomodel.html), введите дату и выберите параметры</h3>
              <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%' }}>
                <div style={{ width: "45%" }} onClick={() => setIsDataType(!isDataType)}>
                  <label className={true ? "form-lable form-lable_type_auth" : "form-lable form-lable_visibility_hidden form-lable_type_auth"}>Data type<div className="input auth__form__input">{dataType}{isDataType ? <IconChevronDown /> : <IconChevronUp />}</div></label>
                  <PopupChoose isOpen={isDataType} click={setDataType} arr={['FAC', 'Potential', 'sigH']} />
                </div>
                <div style={{ width: "45%" }} onClick={() => setIsHemisphere(!isHemisphere)}>
                  <label className={true ? "form-lable form-lable_type_auth" : "form-lable form-lable_visibility_hidden form-lable_type_auth"}>Hemisphere<div className="input auth__form__input">{hemisphere}{isHemisphere ? <IconChevronDown /> : <IconChevronUp />}</div></label>
                  <PopupChoose isOpen={isHemisphere} click={setHemisphere} arr={['Northern', 'Southern']} />
                </div>
              </div>
              <div style={{ display: 'flex', width: "100%", margin: "40px 0" }}>
                <div className='new' style={{ width: "100%", display: "flex", flexDirection: 'column' }}>
                  <lable style={{ color: "white", margin: '10px 0' }}>Bz, nT:<input onChange={(e) => setiono_bz(e.target.value)} type="number" className="cor_input" max='12' min='-12' style={{ margin: '0 10px' }}></input>(-12...12)</lable>
                  <lable style={{ color: "white", margin: '10px 0' }}>By, nT:<input onChange={(e) => setiono_by(e.target.value)} type="number" className="cor_input" max='12' min='-12' style={{ margin: '0 10px' }}></input>(-12...12)</lable>
                  <lable style={{ color: "white", margin: '10px 0' }}>Kp, units:<input onChange={(e) => setiono_kp(e.target.value)} type="number" className="cor_input" max='8' min='1' style={{ margin: '0 10px' }}></input>(1...8)</lable>
                </div>
                <div className='new' style={{ width: "100%", display: "flex", flexDirection: 'column' }}>
                  <lable style={{ color: "white", margin: '10px 0' }}>F107, units:<input onChange={(e) => setiono_f107(e.target.value)} type="number" className="cor_input" min='50' max='350' style={{ margin: '0 10px' }}></input>(50...350)</lable>
                  <lable style={{ color: "white", margin: '10px 0' }}>DOY, days:<input onChange={(e) => setiono_doy(e.target.value)} type="number" className="cor_input" min='1' max='356' style={{ margin: '0 10px' }}></input>(1...356)</lable>
                  <lable style={{ color: "white", margin: '10px 0' }}>UT, decimal hours:<input onChange={(e) => setiono_ut(e.target.value)} type="number" className="cor_input" /* min='0.0' max='23.9' */ step="any" style={{ margin: '0 10px' }}></input>(0.0...23.9)</lable>
                </div>
              </div>
            </div>
            <div className="getChekbox">
              <input onChange={() => setionomodel_n(!ionomodel_n)} type="checkbox" id="how-get" />
              <label className="check">ionomodel_n</label>
            </div>
            <div className="getChekbox">
              <input onChange={() => setionomodel_s(!ionomodel_s)} type="checkbox" id="how-get" />
              <label className="check">ionomodel_s</label>
            </div>
            <div className="date" style={{ display: "flex", flexDirection: "column" }}>
              <h3 className="dateTitle">Если требуется отображение модели Ovation Prime (https://doi.org/10.1029/2009JA014326), введите дату и выберите полусферу (north или south)</h3>
              {(auroral_n || auroral_s) && <div><div style={{ display: "flex", alignItems: "center" }}>
                <input onChange={handleAurorDate} type="date" className="date_input"></input>
                <input onChange={handleAurorTime} type="time" className="date_input"></input>
                <p>UTC</p>
              </div>
                {(!auroral_time || !auroral_date) && <div style={{ color: 'red' }}>Заполните обязательное поле</div>}
              </div>}
            </div>
            <div className="getChekbox">
              <input onChange={() => setauroral_n(!auroral_n)} type="checkbox" id="how-get" />
              <label className="check">auroral_n</label>
            </div>
            <div className="getChekbox">
              <input onChange={() => setauroral_s(!auroral_s)} type="checkbox" id="how-get" />
              <label className="check">auroral_s</label>
            </div>
            <div className="date" style={{ display: "flex", flexDirection: "column" }}>
              <h3 className="dateTitle">Если требуется увеличение проекции до определенного прямоугольника, введише широту (lat) и долготу (lon)</h3>
              <div style={{ width: "100%", display: "flex" }}>
                <lable style={{ color: "white" }}>min_lon<input onChange={(e) => setprojCorMinD(e.target.value)} type="number" className="cor_input"></input></lable>
                <lable style={{ color: "white" }}>max_lon<input onChange={(e) => setprojCorMaxD(e.target.value)} type="number" className="cor_input"></input></lable>
                <lable style={{ color: "white" }}>min_lat<input onChange={(e) => setprojCorMinS(e.target.value)} type="number" className="cor_input"></input></lable>
                <lable style={{ color: "white" }}>max_lat<input onChange={(e) => setprojCorMaxS(e.target.value)} type="number" className="cor_input"></input></lable>
              </div>
            </div>
            <div className="littersWrap" onClick={openObs}>
              <h3 className="dateTitle">Или же укажите код обсерватории INTERMAGNET и радиус в градусах, в пределах которого требуются значения пролета спутника SWARM</h3>
              <label className="form-lable form-lable_type_auth">obs_code<div className="input auth__form__input">{obs_code ? obs_code : "выберите тип"}{isObs_code ? <IconChevronDown /> : <IconChevronUp />}</div></label>
              <PopupChoose isOpen={isObs_code} click={onObs} arr={obs} />
            </div>
            <label style={{ margin: "15px 0 0 0" }} className={clsx({ "form-lable form-lable_type_auth": true, "hide": !obs_code })}>deg_radius<input onChange={(e) => setdeg_radius(e.target.value)} style={{ margin: "0 0 0 15px" }} autoComplete="off" type="number" name="deg_radius" className="cor_input" placeholder={deg_radius} /></label>
            {/* <div className="getChekbox">
              <input onChange={() => setcut_obs_swarm_value(!cut_obs_swarm_value)} type="checkbox" id="how-get" />
              <label className="check">cut_obs_swarm_value</label>
            </div> */}
            <div className="getChekbox">
              <input onChange={() => setigrf_vector_diff(!igrf_vector_diff)} type="checkbox" id="how-get" />
              <label style={{ width: "50%" }} className="check">Отображение разности векторов продольных токов между реальными данными, полученные со спутника SWARM, и модели IGRF-13</label>
            </div>
            <div className="getChekbox">
              <input onChange={() => setmeasure_mu(!measure_mu)} type="checkbox" id="how-get" />
              <label className="check">ДМА мера аномальности значений пролета спутника SWARM</label>
            </div>
            {!isPlot() && <div className="getChekbox">
              <input onChange={() => setmag_grid(!mag_grid)} type="checkbox" id="how-get" />
              <label className="check">Отображение геомагнитной сетке координат</label>
            </div>}
            <div className={clsx({ "getChekbox": true, "hide": !isPlot() })}>
              <input onChange={() => setnear_auroral_points(!near_auroral_points)} type="checkbox" id="how-get" />
              <label className="check">Отображение на графике значений модели Ovation Prime (https://doi.org/10.1029/2009JA014326)</label>
            </div>
          </div>
        </div>
        <div style={{ display: 'flex' }}>
          <button style={{marginRight: '15px'}} onClick={(e) => onSubmit(e, false)} type="submit" className={true ? "auth__submit-btn auth__submit-btn_type_register" : "auth__submit-btn auth__submit-btn_type_login"} disabled={isDisabled()}>{true ? 'Отправить' : "Войти"}</button>
          <button onClick={(e) => onSubmit(e, true)} type="submit" className={true ? "auth__submit-btn auth__submit-btn_type_register" : "auth__submit-btn auth__submit-btn_type_login"} disabled={isDisabled()}>{true ? 'Получить данные в текстовом виде' : "Войти"}</button>
        </div>
        {isDisabled() && <div style={{ color: 'red' }}>Необходимо заполнить обязательные поля</div>}
      </form>
    </main>
  );
}

export default AuthForm;