import React from 'react';
import './PopupChoose.css';
import clsx from 'clsx';


function PopupChoose({arr, click, isOpen}) {
  return(
  <ul className={clsx("choose", isOpen && "choose_active")}>
    {arr.map((i) => {
      return <li onClick={() => click(i)} key={i} className="chooseField">{i}</li>
    })}
  </ul>
  );
}

export default PopupChoose;